import React, { Component } from 'react';
import { GET_MEDIA_NAME } from "../../../../../Services/Utils/utils";
import PinchZoomPan from "react-image-zoom-pan";
import { baseUrl } from '../../../../../Assets/AssetsHelper';
const Media = React.lazy(() => import('../../../CommonComponents/Media/Media'));
const PopUpVideo = React.lazy(() => import('../../../CommonComponents/Media/Elements/PopUpVideo'));
const PopUpAudio = React.lazy(() => import('../../../CommonComponents/Media/Elements/PopUpAudio'));


class ModalMedia extends Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1,
            size:{
                height:this.props.modalAssetData.mediaHeight,
                width:this.props.modalAssetData.mediaWidth
            }
        };
    }
    onErrorImageUpdate = event => {
        const { modalAssetData } = this.props
        if(this.state.count == 1){
            event.target.src = modalAssetData.postFile
            this.setState({
                count: 2,
            })

        }else{
            event.target.src = baseUrl(`media/images/error/image.svg`);
            this.setState({
                count: 3,
                size:{
                    height:400,
                    width:400
                }
            })
        }
    }
    render() {
        const { modalAssetData } = this.props
        const { size } = this.state
        console.log('count', this.state.count)
        return (
            <div className="assets_modal_img_outer">
                <div className={`assets_modal_img_wrapper ${this.state.count == 3 ? 'disabled' : ''}`}>
                    {(modalAssetData.type == 2 || modalAssetData.type == 4) ?
                        <PinchZoomPan maxScale={5} minScale={0.2} step={5} position={'center'} zoomButtons={false} doubleTapBehavior={'reset'}>
                            <img className="assets_modal_media__" src={modalAssetData.publishImage} height={size.height} width={size.width} alt={GET_MEDIA_NAME(modalAssetData.postFile)} onError={this.onErrorImageUpdate} />
                        </PinchZoomPan>

                        : (modalAssetData.type == 1) ? <Media textContent={modalAssetData.content} mediaType={modalAssetData.type == 1} rating={modalAssetData.rating} asset={modalAssetData} animation={'fade'} socialNetwork={modalAssetData.networkId} key={`post_image${modalAssetData.id}`} /> : (modalAssetData.type == 3 || modalAssetData.type == 5) ? modalAssetData.networkId != 12 ? <PopUpVideo data={modalAssetData} /> : <PopUpAudio data={modalAssetData} /> : null}
                </div>
            </div>
        );
    }
}

export default ModalMedia;